.breadcrumbs {
  margin-top: 45px;
}
.mobil__return {
  display: none;
}
.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
}

.row {
  display: flex;
  gap: 10px;
}

.item {
  font-weight: 300;
  line-height: 17px;
  color: #3f4e65;
  letter-spacing: 0.02em;
}

.item:hover {
  text-decoration: underline;
}

.categories__select {
  font-weight: 300;
  line-height: 17px;
  color: #3f4e65;
  opacity: 0.5;
}

.line {
  height: 16px;
  border: 1px dashed #3f4e65;
  opacity: 0.3;
}

@media (max-width: 945px) {
  .breadcrumbs {
    margin-top: 25px;
  }

  .container {
    max-width: 725px;
  }

  .row {
    gap: 5px;
  }

  .item {
    font-size: 12px;
    line-height: 13px;
  }

  .categories__select {
    font-size: 12px;
    line-height: 13px;
  }

  .line {
    height: 14px;
  }
}

@media (max-width: 440px) {
  .row {
    display: none;
  }
  .container {
    max-width: 360px;
  }
  .mobil__return {
    display: block;
    cursor: pointer;
  }
  .mobil__return::before {
    content: "❮";
    height: 32px;
    padding: 8px 12px;
    width: 32px;
    background: rgba(255, 202, 101, 0.3);
    border-radius: 100px;
    margin-right: 5px;
  }
}
